<template>
    <div>
        <h-msg-box class="customMsgBox" v-model="show">
            <p slot="header">
                <span v-if="inputType==1"></span>
                <span v-else-if="inputType==2"></span>
                <span v-else-if="inputType==3"></span>
                <span v-else-if="inputType==4"></span>
                <span v-else-if="inputType==6"></span>
                <span v-else-if="inputType==7"></span>
                <span v-else-if="inputType==8"></span>
                <span v-else></span>
            </p>
            <h-row >
                <h-col span="5">
                    <label>:</label>
                </h-col>
                <h-col span="19" style="margin-bottom: 15px">
                    <a id="selfAccountExcel" v-if="inputType==1"  @click="downLoadSelfExcel">.xlsx</a>

                    <a id="pubAccountExcel"  v-if="inputType==2"  @click="downLoadPubExcel">.xlsx</a>
                    <div v-if="inputType==3" >
                        <!--<div>-->
                            <!--<a id="transferExcel"   @click="downLoadTransferExcel(1)">.xlsx</a>-->
                        <!--</div>-->
                        <div style="margin: 0 0 8px 0" >
                            <a  @click="downLoadTransferExcel(2)" >.xlsx</a>
                        </div>
                        <div>
                            <a   @click="downLoadTransferExcel(3)">.xlsx</a>
                        </div>
                    </div>

                    <a v-if="inputType==4"  @click="downLoadBankBusinExcel">.xlsx</a>

                    <a v-if="inputType==5"  @click="downLoadAgencyExcel">.xlsx</a>
                    <a v-if="inputType==6"  @click="downLoadTradeDayExcel">.xlsx</a>
                    <a v-if="inputType==7"  @click="downLoadTradeRival">.xlsx</a>
                    <a v-if="inputType==8"  @click="downLoadIssuer">.xlsx</a>
                </h-col>
            </h-row>

            <h-row v-if="inputType == 7 || inputType == 8" class-name="full-h" style="margin-bottom: 5px">
                <h-col span="5"  class-name="full-h">
                    <span>:</span>
                </h-col>
                <h-col v-if="inputType == 7" span="19" class-name="full-h">
                    <span>: 1-2-3-4-OTC5-6-</span>
                </h-col>
                <h-col v-if="inputType == 8" span="19" class-name="full-h">
                    <span>: 1-2-3-4-5-6-7-8-9-a-b-c-</span>
                </h-col>
            </h-row>

            <h-row :gutter="10" class-name="full-h">
                <h-col span="5"  class-name="full-h">
                    <span v-if="inputType==1 || inputType==2">:</span>
                    <span v-else-if="inputType==3">:</span>
                    <span v-else-if="inputType==4">:</span>
                    <span v-else-if="inputType==6">:</span>
                    <span v-else-if="inputType==7">:</span>
                    <span v-else-if="inputType==8">:</span>
                    <span v-else>:</span>
                </h-col>
                <h-col span="19" class-name="full-h">
                    <el-upload
                            style="margin-bottom: 10px"
                            ref="upload" class="drag-in-box" drag
                            :action="uploadUrl" with-credentials
                            :on-success="uploadSuccess" :on-change="changeUpload"
                            :data="uploadData" :auto-upload="false"
                            accept=".xlsx"
                            :limit="limit" :file-list="fileList">
                        <i class="el-icon-upload"></i>
                        <div class="el-upload__text"><em :title="fileTips"></em></div>
                    </el-upload>
                </h-col>
            </h-row>
            <div slot="footer">
                <h-button type="primary" @click="submitUpload"></h-button>
                <h-button type="ghost" style="margin-left: 8px" @click="handleCancel"></h-button>
            </div>
        </h-msg-box>

        <h-msg-box class="customMsgBox"  width="500px" v-model="showResult">
            <p slot="header">
                <span></span>
            </p>
            <h-table stripe  border v-show="errorData.length>0" :height="350" :data="errorData" :columns="accountColums">

            </h-table>
            <div slot="footer">
                <h-button type="primary" @click="confirm"></h-button>
            </div>
        </h-msg-box>
    </div>
</template>

<script>

    export default {
        name: "ImportAccountTransfer",
        props: {
            onImportSuccess: {
                type: Function,
                default() {
                }
            },
        },
        data() {
            return {
                uploadData:{dealFlag:0, subclass:'1',ignore:false},
                fileList:[],
                errorData:[],
                accountColums:[
                    {width: 80,key:"lineNum",title:"",align:"center"},
                    {key:"errorInfo",title:"",align:"center"},
                ],
                errorColumns:[
                    {width: 80,key:"lineNum",title:"",align:"center"},
                    {width: 310,key:"errorInfo",title:"",align:"center"},
                ],
                show:false,
                showResult:false,
                fileTips: '',
                uploadUrl:'',
                inputType:1,
                businessClass:'',
            }
        },
        watch:{
            show:function (show) {
                if(show){
                    this.uploadData.ignore = false;
                    this.openMask();
                }else{
                    this.$refs.upload.clearFiles();
                    this.closeMask();
                }
            },
            showResult:function (value) {
                if (!value) {
                    this.$refs.upload.clearFiles();
                    this.closeMask();
                } else {
                    this.openMask();
                }
            }
        },
        computed:{
            limit(){
                return 2
            }
        },
        methods: {
            changeUpload(file,fileList){
                if(fileList.length == 2){
                    fileList.splice(0,1);
                }
            },
            uploadSuccess(response,file,fileList){
                if (response.success){
                    if(response.msg == ""){
                        this.$alert('','',{
                            confirmButtonText: '',
                            type: 'success',
                            callback: action => {
                                this.show = false;
                            }
                        }).then(() => {
                            this.show = false;
                        });
                    }else{
                        this.$alert(response.msg,'',{
                            confirmButtonText: '',
                            type: 'success',
                            callback: action => {
                                this.show = false;
                            }
                        }).then(() => {
                            this.show = false;
                        });
                    }
                    this.onImportSuccess();
                    this.$refs.upload.clearFiles();
                } else if(response.code=='1120002' && this.inputType == '3'){
                    const h = this.$createElement;
                    let errInfos = [];
                    response.data.forEach(x => {
                        errInfos.push({lineNum: x.lineNum, errorInfo: x.errorInfo});
                    });
                    this.$msgbox({
                        title: '',
                        message: h('p', null, [
                            h('span', null, ''),
                            h('h-table', {
                                style: 'width: 100%', props: {
                                    columns: this.errorColumns,
                                    data: errInfos,
                                    height: 300
                                },
                            })
                        ]),
                        closeOnClickModal: false,
                        showCancelButton: true,
                        confirmButtonText: '',
                        cancelButtonText: '',
                        beforeClose: (action, instance, done) => {
                            if (action === 'confirm') {
                                this.uploadData.ignore = true;
                                file.status = 'ready';
                                done();
                            } else {
                                done();
                            }
                        }
                    }).then(action => {
                        this.$refs.upload.submit();
                    }).catch(() => {
                        this.show = false;
                        this.$refs.upload.clearFiles();
                    });
                }else if(response.code=='1120002' && this.type != '3') {
                    this.show = false;
                    this.showResult = true
                    this.errorData = response.data;
                }else{
                    this.$alert(response.msg,'',{
                        confirmButtonText: '',
                        type: 'error',
                        callback: action => {
                            this.show = false;
                        }
                    }).then(() => {
                        this.show = false;
                    });
                    this.$refs.upload.clearFiles();
                }
            },
            confirm(){
                this.showResult = false;
                this.onImportSuccess();
            },
            submitUpload() {
                this.$refs.upload.submit();
            },
            handleCancel(){
                this.show = false
            },
            showDialog(inputType,businessClass){
                if(inputType == '1'){
                    this.uploadUrl= window.LOCAL_CONFIG.API_HOME+'/uploadAccountExcel.json'
                }else if(inputType == '2'){
                    this.uploadUrl= window.LOCAL_CONFIG.API_HOME+'/uploadPubAccountExcel.json'
                }else if(inputType == '3') {
                    this.uploadUrl= window.LOCAL_CONFIG.API_HOME+'/uploadTransferExcelTemplate.json'
                    this.businessClass = businessClass
                }else if(inputType == '4'){
                    this.uploadUrl= window.LOCAL_CONFIG.API_HOME+'/uploadBankBusinFlagExcelTemplate.json'
                }else if(inputType == '5'){
                    this.uploadUrl = window.LOCAL_CONFIG.API_HOME+'/uploadAgencyExcel.json';
                }else if(inputType == '6'){
                    this.uploadUrl = window.LOCAL_CONFIG.API_HOME+'/uploadMarketTradeDayExcel.json';
                }else if(inputType == '7'){
                    this.uploadUrl = window.LOCAL_CONFIG.API_HOME+"/uploadTradeRivalExcel.json";
                }else if(inputType == '8'){
                    this.uploadUrl = window.LOCAL_CONFIG.API_HOME+"/uploadIssuerExcel.json";
                }
                this.inputType = inputType;
                this.show = true;
            },
            downLoadSelfExcel(){
                window.location.href = window.LOCAL_CONFIG.API_HOME+'/getFundAccountExcelTemplate.json';
            },
            downLoadPubExcel(){
                window.location.href = window.LOCAL_CONFIG.API_HOME+'/getPubAccountExcelTemplate.json';
            },
            downLoadTransferExcel(value){
                window.location.href = window.LOCAL_CONFIG.API_HOME+'/getTransferExcelTemplate.json?type='+value +'&businessClass='+this.businessClass;
            },
            downLoadBankBusinExcel(){
                window.location.href = window.LOCAL_CONFIG.API_HOME+'/getBankBusinExcelTemplate.json';
            },
            downLoadAgencyExcel(){
                window.location.href = window.LOCAL_CONFIG.API_HOME+'/getAgencyTemplate.json';
            },
            downLoadTradeDayExcel(){
                window.location.href = window.LOCAL_CONFIG.API_HOME+'/getMarketTradeDayTemplate.json';
            },
            downLoadTradeRival(){
                window.location.href = window.LOCAL_CONFIG.API_HOME+'/getTradeRivalTemplate.json';
            },
            downLoadIssuer(){
                window.location.href = window.LOCAL_CONFIG.API_HOME+'/getIssuerTemplate.json';
            }
        }
    }
</script>

<style lang="scss" src="@/style/common.scss"></style>
<style lang="scss">
    .drag-in-box {

        .el-upload {
            display: block;
            height: 100%;

            .el-upload-dragger {
                width: 100%;
                height: 100%;
            }
        }
    }
</style>


