<template>
    <div class="full-h" >     <!-- class CSS-->
        <h-split v-model="split" min="130" max="600" @on-moving="onSplitMove">
            <!-- -->
            <div slot="left" span="4" class="full-h">
                <!--   span4-->
                <!-- fund-tree FundTree.vue.  <fund-tree></fund-tree> -->
                <!-- jsFundTree.vue  components  FundTree. -->
                <!-- :onFundClick="fundClick"  fundClick.-->
                <!-- ref="treeFund"  fundClick.-->
                <fund-tree  :showAll="true"  :onFundClick="fundClick" ref="treeFund"></fund-tree>
            </div>
            <!-- -->
            <div slot="right" span="20" class="full-h" style="padding-left: 10px" id="full-h-wrapper">
                <!-- -->
                <h-row>
                    <h-col>
                        <!-- form  -->
                        <el-form style="font-size: 12px;":inline="true" size="mini" :model="queryForm"  class="demo-form-inline" >
                            <el-form-item label="" >
                                <h-select v-model="queryForm.copBusinFlag" filterable clearable>
                                    <h-option v-for="item in feeBusinList" :value="item.code" :label="item.text" :key="item.code"></h-option>
                                </h-select>
                            </el-form-item>
                            <el-form-item>
                                <el-button type="primary" @click="refreshFeeRule"></el-button>
                            </el-form-item>
                        </el-form>
                    </h-col>
                </h-row>
                <!-- -->
                <h-button-group>
                    <h-button icon="add" @click="feeRuleAdd"></h-button>
                    <h-button icon="editor" @click="feeRuleModify"></h-button>
                    <h-button icon="delete" @click="feeRuleDelete"></h-button>
                </h-button-group>
                <!--   :height :pageSize  hasPage   :columns="feeCurrentColumns"  -->
                <!-- feeCurrentColumns data(){} url="/feeruleset/getFeeRule.json"  -->
                <h-datagrid :height="autoHeight"
                            stripe hasPage highlight-row
                            :auto-load="false" :on-row-db-click="feeRuleSetDbClick"
                            :pageSize="20" :pageSizeOpts="pageSizeOpts"
                            :columns="feeCurrentColumns" :bindForm="queryForm"
                            url="/fee/rule/page.json" ref="datagrid">
                </h-datagrid>
            </div>
        </h-split>
        <!-- , v-model="showFeeRuleDialog" showFeeRuleDialog jsfalsetrue-->
        <h-msg-box class="customMsgBox" v-model="showFeeRuleDialog" width="500" :mask-closable=false>
            <p slot="header">
                <!-- vue if else  -->
                <span v-if="actionType=='add'"></span>
                <span v-else-if="actionType=='modify'"></span>
            </p>
            <h-row class="full-h">
                <h-col span="22" style="margin-right: 5px">
                    <!-- form-->
                    <h-form :model="feeRuleInfo" :label-width="110" ref="feeRuleForm">
                        <!-- formrequired prop????-->
                        <h-form-item required label="" prop="fund_id_list">
                            <div>
                                <h-select style="width: 250px" v-model="feeRuleInfo.fund_id_list" hideMult isString multiple filterable showBottom>
                                    <h-option v-for="item in fundList" :value="item.code" :label="item.text" :key="item.code"></h-option>
                                </h-select>
                                <h-button type="primary" style="float:right;height:30px;margin-top:3px;" @click="choseFundList"></h-button>
                            </div>
                        </h-form-item>
                        <h-form-item required label="" prop="cop_busin_flag">
                            <h-select v-model="feeRuleInfo.cop_busin_flag" filterable>
                                <h-option v-for="item in feeBusinList" :value="item.code" :label="item.text"
                                          :key="item.code"></h-option>
                            </h-select>
                        </h-form-item>
                        <h-form-item required label="" prop="pay_period_type">
                            <h-select v-model="feeRuleInfo.pay_period_type" filterable>
                                <h-option v-for="item in payPeriodTypeList" :key="item.code" :label="item.text"
                                          :value="item.code.toString()"></h-option>
                            </h-select>
                        </h-form-item>
                        <h-form-item required label="" prop="next_trade_date">
                            <h-row>
                            <h-col span="15" style="margin-right: 35px">
                                <!-- date  -->
                                <h-date-picker v-model="feeRuleInfo.next_trade_date" format="yyyy-MM-dd"
                                               type="date" placeholder=""
                                               @on-change="tradeDateChange"></h-date-picker>
                            </h-col>
                            <h-col span="5">
                                <h-checkbox v-model="trade_date_rule"  @on-change="tradeDateRuleChange">
                                    <span title="293031 "></span>
                                </h-checkbox>
                            </h-col>
                            </h-row>
                        </h-form-item>
                        <h-form-item required label="" prop="pay_date_type">
                            <h-select v-model="feeRuleInfo.pay_date_type" filterable>
                                <h-option v-for="item in payDateTypeList" :key="item.code" :label="item.text" :value="item.code.toString()"></h-option>
                            </h-select>
                        </h-form-item>
                        <h-form-item required label="" prop="require_pay_date">
                            <h-input v-model="feeRuleInfo.require_pay_date" placeholder="/"
                                     @on-keyup="payDateRule" ref="requirePayDate"></h-input>
                        </h-form-item>
                        <h-form-item required label="" prop="subject_no_list">
                            <h-input type="text" v-model="feeRuleInfo.subject_no_list" placeholder=""></h-input>
                        </h-form-item>
                        <h-form-item required label="" prop="pre_frozen_days">
                            <h-select v-model="feeRuleInfo.pre_frozen_days" filterable>
                                <h-option v-for="item in preFrozenDaysList" :key="item.code" :label="item.text" :value="item.code.toString()"></h-option>
                            </h-select>
                        </h-form-item>
                    </h-form>
                </h-col>
            </h-row>
            <div slot="footer">
                <h-button type="ghost" style="margin-left: 8px" @click="feeRuleCancel"></h-button>
                <h-button type="primary" @click="feeRuleBusin"></h-button>
            </div>
        </h-msg-box>
        <fund-choose-msg-box
                @saveFundInfo="saveFundInfo"
                ref="feeRuleFundChooseMsgBox">
        </fund-choose-msg-box>
    </div>
</template>

<script>
    /**** import jsjs{}   ***/
    import FundTree from "@/components/FundTree";               //FundTreeFundTree
    import HDatagrid from "../../../components/HDatagrid";     //HDatagridHDatagrid
    import Cookies from 'js-cookie'

    import {
        getBankList,
        getFundInfo,
        getBusinFlagList,
        parseDateStr,
        showText,
        getMonthLastDay,
        parseDateString,
        getDictList
    } from "@/api/utils";

    import {
        addFeeRule,
        modifyFeeRule,
        deleteFeeRule,
        discardFeeRule
    } from "@/api/feemanage/feerulessetjs";
    import FundChooseMsgBox from "../../../components/FundChooseMsgBox";


    export default {           // @unknow
        name: "feeruleset",    // @unknow
        // data(){return {}} 
        data() {
            return {
                split: 0.15,
                autoHeight: 0,
                pageSizeOpts: [25, 50, 75, 100],
                showFeeRuleDialog: false, /****false showFeeRuleDialog  boolean  ***/
                actionType: '',   /****'' actionType    ***/
                fundList: [],     /****[] fundList     ***/
                feeBusinList: [],
                trade_date_rule:false,
                sysDate: Cookies.get('CurrSysDate'),    /****Cookies Cookies***/
                queryForm: {      /****queryForm: { }  Java.***/
                    fundIds: '',
                    copBusinFlag: ''
                },
                bankInfo: {
                    trust_flag : '1'
                },
                feeCurrentColumns: [
                    {
                        type: 'selection',
                        width: 30,
                        align: "left"
                    }, {
                        title: "Id",
                        key: "fund_id_list",
                        hiddenCol: true,
                        align: "left",
                        width: 95,
                        ellipsis: true
                    }, {
                        title: "",
                        key: "fund_code_list",
                        hiddenCol: false,
                        align: "left",
                        width: 135,
                        ellipsis: true
                    },  {
                        title: "",
                        key: "fund_name_list",
                        align: "left",
                        ellipsis: true
                    }, {
                        title: "",
                        key: "cop_busin_flag",
                        align: "left",
                        width: 95,
                        ellipsis: true,
                        render: (h, params) => {
                            const rowData = params.row;
                            var ccop_busin_flag_name = showText(rowData.cop_busin_flag, this.feeBusinList);
                            return h("span", {}, ccop_busin_flag_name);
                        }
                    }, {
                        title: "",
                        key: "pay_period_type",
                        hiddenCol: false,
                        align: "left",
                        width: 85,
                        ellipsis: true,
                        render: (h, params) => {
                            const rowData = params.row;
                            var pay_period_type_name = showText(rowData.pay_period_type, this.payPeriodTypeList);
                            return h("span", {}, pay_period_type_name);
                        }
                    }, {
                        title: "",
                        key: "pay_date_type",
                        align: "left",
                        width: 95,
                        ellipsis: true,
                        render: (h, params) => {
                            const rowData = params.row;
                            var pay_date_type_name = showText(rowData.pay_date_type, this.payDateTypeList);
                            return h("span", {}, pay_date_type_name);
                        }
                    }, {
                        title: "",
                        key: "require_pay_date",
                        align: "left",
                        width: 85,
                        ellipsis: true
                    }, {
                        title: "",
                        key: "subject_no_list",
                        align: "left",
                        ellipsis: true
                    }, {
                        title: "",
                        key: "pre_trade_date",
                        align: "left",
                        width: 95,
                        ellipsis: true,
                        render: (h, params) => {
                            const rowData = params.row;
                            var date = parseDateString(rowData.pre_trade_date, null);
                            return h("span", {}, date);
                        }
                    }, {
                        title: "",
                        key: "next_trade_date",
                        align: "left",
                        width: 95,
                        ellipsis: true,
                        render: (h, params) => {
                            const rowData = params.row;
                            var date = parseDateString(rowData.next_trade_date, null);
                            return h("span", {}, date);
                        }
                    }, {
                        title: "",
                        key: "pre_frozen_days",
                        align: "left",
                        width: 95,
                        ellipsis: true,
                        render: (h, params) => {
                            const rowData = params.row;
                            var preFrozenDaysName = showText(rowData.pre_frozen_days, this.preFrozenDaysList);
                            return h("span", {}, preFrozenDaysName);
                        }
                    }, {
                        title: "",
                        key: "fee_rule_flag",
                        hiddenCol: true,
                        align: "left",
                        width: 55,
                        ellipsis: true,
                        render: (h, params) => {
                            const rowData = params.row;
                            var fee_rule_flag_name = showText(rowData.fee_rule_flag, this.feeRuleFlagList);
                            return h("span", {}, fee_rule_flag_name);
                        }
                    }, {
                        title: "",
                        key: "serial_no",
                        hiddenCol: true,
                        align: "left",
                        width: 95,
                        ellipsis: true
                    }, {
                        title: "",
                        key: "trade_date_rule_value",
                        hiddenCol: true,
                        align: "left",
                        width: 95,
                        ellipsis: true
                    }

                ],
                feeRuleInfo: {
                    fund_id_list: '',
                    pay_period_type: '',
                    pay_date_type: '',
                    require_pay_date: '',
                    subject_no_list: '',
                    next_trade_date: '',
                    pre_frozen_days: '',
                    serial_no: '',
                    trade_date_rule_value: ''
                },
                payPeriodTypeList: [],
                payDateTypeList: [],
                feeRuleFlagList :[
                    {
                        code: '0',
                        text: ''
                    }, {
                        code: '1',
                        text: ''
                    }
                ],
                preFrozenDaysList :[
                    {
                        code: '0',
                        text: ''
                    }, {
                        code: '1',
                        text: 'T-1'
                    }
                    , {
                        code: '2',
                        text: 'T-2'
                    }
                ]
            }
        },
        watch:{
            showFeeRuleDialog:function (show) {
                if(show){
                    this.openMask()
                }else{
                    this.closeMask()
                }
            }
        },
        activated(){
            getBusinFlagList(22).then(res => this.feeBusinList = res.data);
            getFundInfo('/common/fundName.json', null).then(res=>this.fundList=res.data);
        },
        created() {
            getDictList(91182).then(res => this.payDateTypeList = res.data);
            getDictList(60074).then(res => this.payPeriodTypeList = res.data);
        },
        mounted() {  //
            this.re_layout();
        },
        methods: {  //
            re_layout(){
                let wrapper0Height = document.getElementById("wrapper-0") ? document.getElementById("wrapper-0").offsetHeight : 0;
                this.autoHeight = document.getElementById("full-h-wrapper").offsetHeight - wrapper0Height - 100;
            },
            fundClick(data) {
                if (data) {
                    this.queryForm.fundIds = data.code;
                    if (data.code.indexOf(",") < 0) {
                        this.refreshFeeRule();
                    } else {
                        this.queryForm.fundIds = "";
                        this.refreshFeeRule();
                    }
                }
            },
            feeRuleAdd() {
                //
                this.$refs.feeRuleForm.resetFields();
                trade_date_rule: false,
                this.feeRuleInfo = {
                    serial_no: '',
                    fund_id_list: '',
                    cop_busin_flag: '',
                    pay_period_type: '',
                    pay_date_type: '2',
                    require_pay_date: '',
                    subject_no_list: '',
                    next_trade_date: '',
                    pre_frozen_days: '1',
                    trade_date_rule_value: ''
                }
                this.showFeeRuleDialog = true;
                this.actionType = "add";
            },
            feeRuleModify() {
                let selects = this.$refs.datagrid.$refs.gridContent.getSelection();   //
                if (selects.length == 0) {
                    this.openMask();
                    this.$alert('', '', {
                        confirmButtonText: '',
                        type: 'error',
                        callback:action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    })
                    return
                }
                this.feeRuleSetDbClick(selects[0]);
            },
            feeRuleSetDbClick(data){
                this.showFeeRuleDialog = true;
                this.actionType = "modify";
                this.feeRuleInfo = data;
                if (this.feeRuleInfo.trade_date_rule_value == '1') {
                    this.trade_date_rule = true;
                }
                this.feeRuleInfo.fund_id_list = this.feeRuleInfo.fund_id_list.toString();
                this.feeRuleInfo.cop_busin_flag = this.feeRuleInfo.cop_busin_flag.toString();  //@unknow
                this.feeRuleInfo.next_trade_date = this.feeRuleInfo.next_trade_date.toString();  //@unknow
            },
            feeRuleDelete() {
                let selects = this.$refs.datagrid.$refs.gridContent.getSelection();
                if (selects.length == 0) {
                    this.openMask();
                    this.$alert('', '', {
                        confirmButtonText: '',
                        type: 'error',
                        callback:action => {
                            this.closeMask();
                        }
                    }).then(() => {
                        this.closeMask();
                    })
                    return
                }
                var serial_nos = '';
                for (var i = 0; i < selects.length; i++) {
                    serial_nos += selects[i].serial_no + ',';
                }
                serial_nos = serial_nos.substring(0, serial_nos.length - 1);
                this.openMask();
                this.$confirm('', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    deleteFeeRule(serial_nos).then(res => {
                        if (res.data && res.data.code == '0') {
                            this.$alert(res.data.msg, '', {
                                confirmButtonText: '',
                                type: "success",
                                callback:action => {
                                    this.closeMask();
                                }
                            }).then(() => {
                                this.closeMask();
                            })
                            this.refreshFeeRule();
                        }else {
                            this.$alert(res.data.msg, '', {
                                confirmButtonText: '',
                                type: 'error',
                                callback:action => {
                                    this.closeMask();
                                }
                            }).then(() => {
                                this.closeMask();
                            })
                        }
                    });
                });
            },
            feeRuleDiscard(){
                let selects = this.$refs.datagrid.$refs.gridContent.getSelection();
                if (selects.length == 0) {
                    this.$alert('', '', {
                        confirmButtonText: '',
                        type: 'error'
                    });
                    return
                }
                var serial_nos = '';
                for (var i = 0; i < selects.length; i++) {
                    serial_nos += selects[i].serial_no + ',';
                }
                serial_nos = serial_nos.substring(0, serial_nos.length - 1);
                this.$confirm('', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'

                }).then(() => {
                    discardFeeRule(serial_nos).then(res => {
                        if (res.data && res.data.code == '0') {
                            this.$alert(res.data.msg, '', {
                                confirmButtonText: '',
                                type: "success"
                            });
                            this.refreshFeeRule();
                        }else {
                            this.$alert(res.data.msg, '', {
                                confirmButtonText: '',
                                type: 'error'
                            });
                        }
                    });
                });
            },
            refreshFeeRule() {
                //
                this.$refs.datagrid.dataChange();
            },
            payDateRule() {
                var resultFlag = true;
                if (this.feeRuleInfo.pay_date_type == "2") {
                    if (!(this.feeRuleInfo.require_pay_date >= 1 && this.feeRuleInfo.require_pay_date <= 23)) {
                        this.$refs.requirePayDate.blur();
                        resultFlag = false;
                        this.$alert('123',"", {
                            confirmButtonText: '',
                            type: 'error'
                        })
                    }
                } else if (this.feeRuleInfo.pay_date_type == "1") {
                    if (!(this.feeRuleInfo.require_pay_date >= 1 && this.feeRuleInfo.require_pay_date <= 28)) {
                        this.$refs.requirePayDate.blur();
                        resultFlag = false;
                        this.$alert('128',"", {
                            confirmButtonText: '',
                            type: 'error'
                        })
                    }
                } else {
                    this.$refs.requirePayDate.blur();
                    resultFlag = false;
                    this.$alert('','', {
                        confirmButtonText: '',
                        type: 'error'
                    });
                    this.feeRuleInfo.require_pay_date = '';
                }
                return resultFlag;
            },
            feeRuleCancel() {
                this.showFeeRuleDialog = false;
            },
            feeRuleBusin() {
                let isValid = true;
                if(!this.payDateRule()){
                    return ;
                }
                this.$refs.feeRuleForm.validate((validate) => isValid = isValid && validate);
                if (!isValid) {
                    this.$alert('', '', {
                        confirmButtonText: '',
                        type: 'error'
                    })
                    return
                }
                this.feeRuleInfo.next_trade_date = parseDateStr(this.feeRuleInfo.next_trade_date, "yyyyMMdd");
                if (this.actionType == 'add') {
                    addFeeRule(this.feeRuleInfo).then(res => {
                        if (res.data && res.data.code == '0') {
                            this.$alert(res.data.msg, '', {
                                confirmButtonText: '',
                                type: "success",
                                callback:action => {
                                    this.showFeeRuleDialog = false;
                                }
                            }).then(() => {
                                this.showFeeRuleDialog = false;
                            })
                            this.refreshFeeRule();
                        }else {
                            this.$alert(res.data.msg, '', {
                                confirmButtonText: '',
                                type: 'error'
                            })
                        }
                    });
                }
                if (this.actionType == 'modify') {
                    modifyFeeRule(this.feeRuleInfo).then(res => {
                        if (res.data && res.data.code == '0') {
                            this.$alert(res.data.msg, '', {
                                confirmButtonText: '',
                                type: "success",
                                callback:action => {
                                    this.showFeeRuleDialog = false;
                                }
                            }).then(() => {
                                this.showFeeRuleDialog = false;
                            })
                            this.refreshFeeRule();
                        }else {
                            this.$alert(res.data.msg, '', {
                                confirmButtonText: '',
                                type: 'error'
                            })
                        }
                    });
                }

            },
            tradeDateRuleChange() {
                if (this.trade_date_rule) {
                    var date = this.sysDate;
                    if (this.feeRuleInfo.next_trade_date && this.feeRuleInfo.next_trade_date != '') {
                        date = parseDateStr(this.feeRuleInfo.next_trade_date);
                    }
                    this.feeRuleInfo.next_trade_date = getMonthLastDay(date);
                    this.feeRuleInfo.trade_date_rule_value = '1';
                } else {
                    this.feeRuleInfo.next_trade_date = '';
                    this.feeRuleInfo.trade_date_rule_value = '';
                }
            },
            tradeDateChange() {
                var date = parseDateStr(this.feeRuleInfo.next_trade_date);
                if (parseInt(date.substring(6, 8)) <= 28 || date == '' || !date) {
                    this.feeRuleInfo.trade_date_rule_value = '';
                    this.trade_date_rule = false;
                } else {
                    this.trade_date_rule = true;
                    this.feeRuleInfo.trade_date_rule_value = '1';
                    this.feeRuleInfo.next_trade_date = getMonthLastDay(date);
                }
            },
            choseFundList(){
                let fullWrapperHieght = document.getElementById("full-h-wrapper").offsetHeight;
                this.$refs.feeRuleFundChooseMsgBox.showMsgBox(this.feeRuleInfo.fund_id_list, fullWrapperHieght, '1');
            },
            saveFundInfo(data){
                this.feeRuleInfo.fund_id_list = data;
            },
            onSplitMove(){
                this.$refs.datagrid.handleResize();
            }
        },
        components: {
            FundChooseMsgBox, //
            FundTree,
            HDatagrid
        }
    }
</script>





