<template>
    <div>
        <h-msg-box class="customMsgBox" v-model="show" :footerHide="footerHide">
            <p slot="header">
                <span v-if="fileType==1">TA</span>
                <span v-if="fileType==2"></span>
            </p>

            <div slot="footer">
                <h-button type="primary" @click="submitUpload"></h-button>
                <h-button v-show="fileType==1" type="primary" @click="revokeTA"></h-button>
                <h-button type="ghost" style="margin-left: 8px" @click="handleCancel"></h-button>
            </div>

            <!--TA-->
            <h-tabs size="small" v-model="tabsActive" @on-click="tabsClick" v-show="fileType == 1">
                <h-tab-pane label="">
                    <el-upload v-show="fileType == 1"
                            style="margin-bottom: 10px"
                            ref="uploadTA"
                            class="drag-in-box" drag
                            :action="uploadUrl"
                            :multiple="fileType!=1"
                            with-credentials
                            :on-success="uploadSuccess"
                            :data="uploadData"
                            :auto-upload="false"
                            :on-change="uploadChange"
                            :file-list="fileList"
                            :limit="limit">
                        <i class="el-icon-upload"></i>
                        <div class="el-upload__text"><em :title="fileTips"></em></div>
                        <!--<el-button :title="fileTips"-->
                        <!--slot="trigger" size="small" type="primary"></el-button>-->
                    </el-upload>
                    <h-form :label-width="100" cols="1">
                        <h-form-item  v-if="fileType==1" :label-width="80" required label="">
                            <el-radio v-model="uploadData.subclass" label="1">TA</el-radio>
                            <el-radio v-model="uploadData.subclass" label="2">TA</el-radio>
                        </h-form-item>
                    </h-form>
                </h-tab-pane>
                <h-tab-pane label="">
                    <h-datagrid stripe
                                highlight-row
                                :height="importResultHeight"
                                :columns="importResultColumns"
                                :gridData="tData"
                                ref="importResultDatagrid">
                    </h-datagrid>
                </h-tab-pane>
            </h-tabs>

            <!---->
            <el-upload v-show="fileType == 2"
                    style="margin-bottom: 10px"
                    ref="uploadApply"
                    class="drag-in-box" drag
                    :action="uploadUrl"
                    :multiple="fileType!=1"
                    with-credentials
                    :on-success="uploadSuccess"
                    :data="uploadData"
                    :auto-upload="false"
                    :on-change="uploadChange"
                    :file-list="fileList"
                    accept=".xls,.xlsx"
                    :limit="limit">
                <i class="el-icon-upload"></i>
                <div class="el-upload__text"><em :title="fileTips"></em></div>
            </el-upload>
            <div v-if="fileType == 2">
                <span v-themed:text></span>
                <h-checkbox v-model="uploadData.isNextDay"></h-checkbox>
            </div>

        </h-msg-box>

        <h-msg-box class="customMsgBox"  width="800px" v-model="showResult">
            <p slot="header">
                <span></span>
            </p>
            <div v-show="fileType==2">{{successCount}}{{errorCount}}</div>
            <div v-show="fileType==2&&showContinueBtn" style="color:red">()</div>
            <div v-show="fileType==1&&showContinueBtn" style="color:red">()</div>

            <h-table stripe  border v-show="errorData.length>0" :height="350" :data="errorData" :columns="columns">

            </h-table>
            <div slot="footer">
                <h-button type="primary" @click="handleErrorCancel"></h-button>
                <h-button type="primary" style="margin-left: 8px" @click="handleErrorContinue" v-show="showContinueBtn"></h-button>
            </div>
        </h-msg-box>
    </div>
</template>

<script>
    import HDatagrid from '@/components/HDatagrid'
    import {showText, parseDateString, operatorList} from '@/api/utils'
    import {taQuery} from '@/api/transfer/transfer'

    export default {
        name: "ImportDialog",
        components: {
            HDatagrid
        },
        watch:{
            show:function (show) {
                if(show){
                    this.openMask();
                }else{
                    this.closeMask();
                    this.$refs.uploadTA && this.$refs.uploadTA.clearFiles();
                    this.$refs.uploadApply && this.$refs.uploadApply.clearFiles();
                }
            },
            showResult:function (value) {
                if (!value) {
                    if (!this.uploadData.ignore) {
                        this.$refs.uploadTA && this.$refs.uploadTA.clearFiles();
                        this.$refs.uploadApply && this.$refs.uploadApply.clearFiles();
                    }
                }
                this.closeMask();
                if (this.successCount>0){
                    this.openMask();
                    this.onImportSuccess();
                }
            }
        },
        props: {
            onImportSuccess: {
                type: Function,
                default() {
                }
            },
        },
        data() {
            let importResultColumns = [
                {
                    title: "",
                    key: "import_flag",
                    width: 80,
                    ellipsis: true,
                    align: 'left',
                    render: (h, params) => {
                        const rowData = params.row;
                        let statusName = showText(rowData.import_flag, this.importFlagList, {
                            key: 'code',
                            value: 'text'
                        });
                        return h("span", {}, statusName);
                    }
                }, {
                    title: "",
                    key: "load_status",
                    width: 80,
                    ellipsis: true,
                    align: 'left',
                    render: (h, params) => {
                        const rowData = params.row;
                        let statusName = showText(rowData.load_state, this.statusList, {
                            key: 'code',
                            value: 'text'
                        });
                        return h("span", {}, statusName);
                    }
                }, {
                    title: "TA",
                    key: "ta_type",
                    width: 80,
                    ellipsis: true,
                    align: 'left',
                    render: (h, params) => {
                        const rowData = params.row;
                        let statusName = showText(rowData.ta_type, this.taTypeList, {
                            key: 'code',
                            value: 'text'
                        });
                        return h("span", {}, statusName);
                    }
                }, {
                    title: "",
                    key: "file_name",
                    width: 170,
                    ellipsis: true,
                    align: 'left',
                    render: (h, params) => {
                        const rowData = params.row;
                        let fileNameSpan = h('span', {}, rowData.file_name);
                        return h('el-tooltip', {
                            props: {
                                trigger: 'hover',
                                effect: 'light',
                                placement: 'top-start',
                                openDelay: 400,
                                content: rowData.file_name,
                                popperClass: 'tooltipclass'
                            }
                        }, [fileNameSpan]);
                    }
                }, {
                    title: "",
                    key: "operator_no",
                    width: 120,
                    ellipsis: true,
                    align: 'left',
                    render: (h, params) => {
                        const rowData = params.row;
                        let operatorName = showText(rowData.operator_no, this.operatorNoList);
                        return h("span", {}, operatorName);
                    }
                }, {
                    title: "",
                    key: "input_time",
                    width: 150,
                    ellipsis: true,
                    align: 'left',
                    render: (h, params) => {
                        const rowData = params.row;
                        var typeName = parseDateString(rowData.input_date, rowData.input_time);
                        return h("span", {}, typeName);
                    }
                }, {
                    title: "",
                    key: "remark",
                    width: 110,
                    ellipsis: true,
                    align: 'left',
                    render: (h, params) => {
                        const rowData = params.row;
                        let remarkSpan = h('span', {}, rowData.remark);
                        return h('el-tooltip', {
                            props: {
                                trigger: 'hover',
                                effect: 'light',
                                placement: 'top-start',
                                openDelay: 400,
                                content: rowData.remark,
                                popperClass: 'tooltipclass'
                            }
                        }, [remarkSpan]);
                    }
                }];
            return {
                uploadData:{dealFlag:0, subclass:'1',isNextDay: false,ignore:false},
                fileList:[],
                errorData:[],
                columns:[],
                taColumns:[
                    {width: 100,key:"fund_code",title:"",align:"center"},
                    {width: 100,key:"ta_biz_type",title:"",align:"center"},
                    {width: 100,key:"agency_code",title:"",align:"center"},
                    {width: 100,key:"cash_settle_date",title:"",align:"center"},
                    {key:"error_info",title:"",align:"center"}
                ],
                outApplyColumns:[
                    {width: 200,key:"fileName",title:"",align:"center"},
                    {width: 80,key:"lineNum",title:"",align:"center"},
                    {key:"errorInfo",title:"",align:"center"},
                ],
                show:false,
                showResult:false,
                fileTips: '',
                uploadUrl:'',
                fileType:1,
                resultInfo:'',
                errorCount:0,
                successCount:0,
                footerHide: false,
                tabsActive: 0,
                importResultColumns: importResultColumns,
                importResultHeight: 175,
                tData: {rows: []},
                statusList: [
                    {code: '1', text: ''},
                    {code: '2', text: ''}
                ],
                importFlagList: [
                    {code: '1', text: ''},
                    {code: '0', text: ''}
                ],
                taTypeList: [
                    {code: '1', text: 'TA'},
                    {code: '2', text: 'TA'}
                ],
                operatorNoList: [],
                showContinueBtn: false,
            }
        },
        computed:{
            limit(){
                return this.fileType==1? 1: 20
            }
        },
        methods: {
            uploadSuccess(response,file,fileList){

                file.status = "ready";//

                if (this.fileType==1) {
                    if (response.success){
                        if(response.msg == ""){
                            let msg = ''
                            if (this.uploadData.dealFlag == 1)
                                msg = ''
                            this.$alert(msg,'',{
                                confirmButtonText: '',
                                type: 'success',
                                callback:action => {
                                    this.show = false;
                                }
                            }).then(() => {
                                this.show = false;
                            })
                        }else{
                            this.$alert(response.msg,'',{
                                confirmButtonText: '',
                                type: 'success',
                                callback:action => {
                                    this.show = false;
                                }
                            }).then(() => {
                                this.show = false;
                            })
                        }
                        this.onImportSuccess();
                    } else if(response.code=='2'){
                        this.errorData = response.data;

                        //
                        let errorData4NoFundCode = this.errorData.filter(e => e.error_no === -1);
                        if (errorData4NoFundCode && errorData4NoFundCode.length === this.errorData.length) {
                            this.showContinueBtn = true;
                        } else {
                            this.showContinueBtn = false;
                        }
                        this.showResult = true;
                    }else {
                        this.$alert(response.msg,'',{
                            confirmButtonText: '',
                            type: 'error',
                            callback: action => {
                                this.show = false;
                            }
                        }).then(() => {
                            this.show = false;
                        });
                    }

                    if (!this.showContinueBtn) {
                        this.$refs.uploadTA && this.$refs.uploadTA.clearFiles();
                        this.$refs.uploadApply && this.$refs.uploadApply.clearFiles();
                    }
                    this.uploadData.ignore = false;
                }else{
                    if (response.success){
                        this.successCount++;
                    } else {
                        this.errorCount ++
                        this.errorData = this.errorData.concat(response.data)
                    }

                    if (this.successCount === fileList.length) {
                        //
                        this.$alert('','',{
                            confirmButtonText: '',
                            type: 'success',
                            callback:action => {
                                this.show = false;
                            }
                        }).then(() => {
                            this.show = false;
                        });
                        this.uploadData.ignore = false;
                        this.onImportSuccess();
                    } else if (this.successCount + this.errorCount === fileList.length) {

                        //
                        this.fileList = this.fileList.filter(f => this.errorData.find(e => e.errorNo === -1 && e.fileName === f.name));
                        //-1
                        let findContinueData = this.errorData.find(e => e.errorNo === -1);
                        if (findContinueData) {
                            this.showContinueBtn = true;
                        } else {
                            this.showContinueBtn = false;
                        }

                        this.uploadData.ignore = false;
                        this.showResult = true;
                    }
                }
            },
            submitUpload() {
                if (this.fileType==1){
                    this.uploadData.dealFlag = 0;
                    this.columns = this.taColumns;

                    this.$refs.uploadTA.submit();
                } else {
                    this.errorData = []
                    this.errorCount = 0
                    this.successCount = 0
                    this.columns = this.outApplyColumns;

                    this.$refs.uploadApply.submit();
                }
                this.showContinueBtn = false;
                this.uploadData.ignore = false;
            },
            uploadChange(file, fileList){
                if(!file.response){
                    let existFile = fileList.slice(0, fileList.length - 1).find(f => f.name === file.name);
                    if(existFile){
                        this.$alert('!','',{
                            confirmButtonText: '',
                            type: 'error'
                        });
                        fileList.pop()
                    }
                    this.fileList = fileList;
                }
            },
            revokeTA(){
                this.uploadData.dealFlag = 1;

                if (this.fileType == 1) {
                    this.$refs.uploadTA.submit();
                } else if (this.fileType == 2) {
                    this.$refs.uploadApply.submit();
                }
            },
            handleCancel(){
                this.show = false
            },
            showDialog(type){
                if (type==1) {
                    this.uploadUrl= window.LOCAL_CONFIG.API_HOME+'/cashsettle/input/taImport.json'
                    this.fileTips="ASSETCHGYYYYMMDD.txt"
                }else {
                    this.uploadUrl= window.LOCAL_CONFIG.API_HOME+'/cashsettle/input/outApplyImport.json'
                    this.fileTips= "#_#_#.xls\n"
                    //  + ":(EXCEL)\n"
                    // + "              XLS         \n"
                    // + "  ----------------------------------------------------------------------------------\n"
                    // + "                      1\n"
                    // + "                        1\n"
                    // + "                        10002\n"
                    // + "                    \n"
                    // + "                    \n"
                    // + "                      \n"
                    // + "                      \n"
                    // + "                      \n"
                    // + "                  \" "
                }
                this.show = true;
                this.fileType = type;

                this.tabsActive = 0;
                this.importResultHeight = 175;
                this.footerHide = false;
            },
            tabsClick(activeValue) {
                if (activeValue === 0) {
                    this.importResultHeight = 175;
                    this.footerHide = false;
                } else if (activeValue === 1) {
                    this.importResultHeight = 227;
                    this.footerHide = true;

                    operatorList().then(res => this.operatorNoList = res.data);

                    this.tData = {rows: []};
                    taQuery().then(res => {
                        if (res && res.status === 200 && res.data && res.data.success === true) {
                            this.tData.rows = res.data.data;
                        } else {
                            this.$alert(res.data.msg, '', {
                                confirmButtonText: '',
                                type: 'error'
                            });
                        }
                    }).catch(error => {
                        this.$alert(error, '', {
                            confirmButtonText: '',
                            type: 'error'
                        });
                    });
                }
            },
            handleErrorCancel() {
                this.showResult = false;
            },
            handleErrorContinue() {
                this.uploadData.ignore = true;
                this.showResult = false;

                if (this.fileType == 1) {
                    this.$refs.uploadTA.submit();
                } else if (this.fileType == 2) {
                    this.$refs.uploadApply.submit();
                }
            }
        }
    }
</script>

<style lang="scss" src="@/style/common.scss"></style>
<style lang="scss">
    .drag-in-box {

        .el-upload {
            display: block;
            height: 100%;

            .el-upload-dragger {
                width: 100%;
                height: 100%;
            }
        }
    }
</style>


