<template>
<div class="full-h" id="full-h-wrapper">
    <label style="display:none;"><span></span><input type="text" name="hidden1" ></label>
    <label style="display:none;"><span></span><input type="password" name="hidden2" ></label>
    <h-form ref="form" :model="queryForm" cols='5'>

        <h-form-item label="" :label-width="80" prop="cashFlowTypes" >
            <h-select v-model="queryForm.cashFlowTypes" filterable isString multiple>
                <h-option v-for="item in cashFlowTypeList" :key="item.code" :label="item.text" :value="item.code">
                </h-option>
            </h-select>
        </h-form-item>

        <h-form-item label="" :label-width="80" prop="dealNo" >
            <h-input v-model="queryForm.dealNo" type="text"></h-input>
        </h-form-item>

        <h-form-item label="" :label-width="80" prop="fundCodes" >
            <h-select v-model="queryForm.fundCodes" filterable isString multiple>
                <h-option v-for="item in fundList" :key="item.code" :label="item.text" :value="item.code">
                </h-option>
            </h-select>
        </h-form-item>

        <h-form-item label="" :label-width="100" prop="settleDate" >
            <h-date-picker type="date" placeholder="" v-model="cashSettleDate" format="yyyy-MM-dd">
            </h-date-picker>
        </h-form-item>

        <h-form-item :label-width="10" >
            <h-button type="primary" @click="on_form_query"></h-button>
            <h-button type="ghost" @click="on_form_refresh"></h-button>
        </h-form-item>
    </h-form>

    <div class="pb10">
        <h-button-group>
            <!--<h-button @click="clearSuccess"></h-button>
            <h-button @click="clearFail"></h-button>
            <h-button @click="clearBack"></h-button>-->
            <h-button @click="cashModify"></h-button>
            <!--<h-button @click="on_upload"></h-button>
            <h-button @click="on_export"></h-button>-->
        </h-button-group>
        <h-datagrid
                hasPage stripe
                highlight-row :height="autoHeight"
                :pageSize="100" :pageSizeOpts="pageSizeOpts"
                ref="Dtgrd_CFR" url="/cashmgnt/cashflow/query.json"
                :columns="cashFlowReportColumn"
                :bindForm="queryForm">
        </h-datagrid>
    </div>
    <h-msg-box class="customMsgBox" v-model="showChangeCashClearStatusDialog" width="300" :mask-closable=false>
        <p slot="header">
            <span ></span>
        </p>
        <h-form :model="cashFlowInfo" :label-width="80" ref="cashFlowForm">
            <h-form-item required label="" prop="cash_flow_detail_clear_status">
                <h-select v-model="cashFlowInfo.cash_flow_detail_clear_status" filterable>
                    <h-option v-for="item in statusList" :key="item.code" :label="item.text" :value="item.code"></h-option>
                </h-select>
            </h-form-item>
        </h-form>
        <div slot="footer">
            <h-button type="ghost" style="margin-left: 8px" @click="cancel"></h-button>
            <h-button type="primary" @click="save"></h-button>
        </div>
    </h-msg-box>
</div>
</template>

<script>
import {
    showText,
    getDictList,
    getFundInfo,
    parseDateString,
    formatMoney,
    parseDateStr,
    getRivalList
} from "@/api/utils";
import {
    updateClearStatus
} from "@/api/cashmgnt/cashFlow"
import HDatagrid from "../../../components/HDatagrid";
export default {

    components: {
        HDatagrid
    },
    data() {
        return {
            //
            queryForm: {
                cashFlowTypes: '',
                dealNo: '',
                fundCodes: '',
                settleDate: ''
            },
            pageSizeOpts:[25,50,75,100],
            cashFlowModifyInfo:[],
            showChangeCashClearStatusDialog: false,
            cashFlowInfo:{
                cash_flow_detail_clear_status:''
            },
            cashFlowReportColumn: [
                {
                    type: 'selection',
                    width: 30,
                    align: "left"
                }, {
                    title: "",
                    key: "cashFlowDetailId",
                    width: 100,
                    ellipsis: true,
                    sortable:true,
                    align: 'center'
                }, {
                    title: "",
                    key: "fundName",
                    ellipsis: true,
                    align: 'center'
                }, {
                    title: "",
                    key: "insId",
                    width: 100,
                    ellipsis: true,
                    align: 'center'
                }, {
                    title: "",
                    key: "dealNo",
                    width: 100,
                    ellipsis: true,
                    align: 'center'
                }, {
                    title: "",
                    key: "traderivalName",
                    ellipsis: true,
                    align: 'center'
                }, {
                    title: "",
                    key: "cashFlowDetailType",
                    ellipsis: true,
                    align: 'center'
                }, {
                    title: "",
                    key: "itemName",
                    ellipsis: true,
                    align: 'center',
                    width: 120
                }, {
                    title: "",
                    key: "settleDate",
                    width: 100,
                    ellipsis: true,
                    align: 'center'
                }, {
                    title: "",
                    key: "cashChangeDirection",
                    width: 100,
                    ellipsis: true,
                    align: 'center',
                    render: (h,params) =>{
                        const rowData = params.row
                        let direction = showText(rowData.cashChangeDirection,this.directionList);
                        return h("span",{},direction)
                    }
                }, {
                    title: "()",
                    key: "settleBalance",
                    width: 100,
                    ellipsis: true,
                    align: 'center',
                    render: (h, params) => {
                        const rowData = params.row;
                        var name = formatMoney(rowData.settleBalance);
                        return h("span", {
                            style: {
                                fontWeight: 'bold'
                            }
                        }, name);
                    }
                }, {
                    title: "",
                    key: "cashFlowDetailClearStatus",
                    width: 100,
                    ellipsis: true,
                    align: 'center',
                    render: (h,params) => {
                        const rowData = params.row;
                        let status = showText(rowData.cashFlowDetailClearStatus,this.statusList);
                        return h("span",{},status)
                    }
                },  {
                    title: "",
                    key: "entrustDirectionName",
                    ellipsis: true,
                    align: 'center'
                }
            ],
            Data_CashFlowReport: {},
            fundList: [],
            cashFlowTypeList: [
                {
                    code:'1',
                    text:'TA()'
                }, {
                    code:'2',
                    text:'TA()'
                }, {
                    code:'3',
                    text:'TA'
                }, {
                    code:'4',
                    text:'TA'
                }, {
                    code:'5',
                    text:'TA'
                }, {
                    code:'6',
                    text:'TA'
                }, {
                    code:'7',
                    text:''
                }, {
                    code:'8',
                    text:''
                }, {
                    code:'9',
                    text:''
                }, {
                    code:'10',
                    text:''
                }, {
                    code:'11',
                    text:''
                }, {
                    code:'12',
                    text:''
                }, {
                    code:'13',
                    text:''
                }, {
                    code:'14',
                    text:''
                }, {
                    code:'15',
                    text:''
                }, {
                    code:'16',
                    text:''
                }, {
                    code:'17',
                    text:''
                }, {
                    code:'18',
                    text:''
                }, {
                    code:'19',
                    text:''
                }, {
                    code:'20',
                    text:''
                }, {
                    code:'21',
                    text:''
                }, {
                    code:'22',
                    text:''
                }, {
                    code:'23',
                    text:''
                }, {
                    code:'24',
                    text:'()'
                }, {
                    code:'25',
                    text:'()'
                }, {
                    code:'26',
                    text:''
                }, {
                    code:'27',
                    text:''
                }, {
                    code:'28',
                    text:''
                }, {
                    code:'29',
                    text:''
                }, {
                    code:'30',
                    text:''
                }, {
                    code:'31',
                    text:''
                }, {
                    code:'32',
                    text:''
                }, {
                    code:'33',
                    text:''
                }, {
                    code:'34',
                    text:''
                }, {
                    code:'35',
                    text:''
                }, {
                    code:'36',
                    text:''
                }, {
                    code:'37',
                    text:''
                }, {
                    code:'38',
                    text:''
                }, {
                    code:'39',
                    text:''
                }, {
                    code:'40',
                    text:''
                }, {
                    code:'41',
                    text:'()'
                }, {
                    code:'42',
                    text:'()'
                }, {
                    code:'43',
                    text:''
                }
            ],
            rivalCodeList:[],
            directionList:[
                {
                    code:'0',
                    text:''
                }, {
                    code:'1',
                    text:''
                }, {
                    code:'2',
                    text:''
                }
            ],
            loading_query: false,
            autoHeight:0,
            statusList:[
                {
                    code:'0',
                    text:''
                }, {
                    code:'1',
                    text:''
                }, {
                    code:'2',
                    text:''
                }
            ]
        }
    },
    computed:{
        cashSettleDate:{
            get:function () {
                if(this.queryForm.settleDate){
                    return parseDateString(this.queryForm.settleDate);
                }
                return ''
            },
            set:function (newValue) {
                this.queryForm.settleDate = parseDateStr(newValue);
            }
        },
        businClass:{
            get:function () {
                if(this.queryForm.businClass){
                    return this.queryForm.businClass.split(",")
                }
                return []
            },
            set:function (newValue) {
                this.queryForm.businClass = newValue.join(",")
            }
        },
        inputDate:{
            get:function () {
                if(this.queryForm.inputDate){
                    return parseDateString(this.queryForm.inputDate);
                }
                return ''
            },
            set:function (newValue) {
                this.queryForm.inputDate = parseDateStr(newValue);
            }
        }
    },
    activated(){
        getFundInfo('/common/fundNamelist.json?type='+'code').then(res => this.fundList = res.data);
    },
    mounted() {
        this.re_layout();
        getRivalList(0,50).then(res => this.rivalCodeList = res.data)
    },
    methods: {
        on_export() {
            this.$refs['Dtgrd_CFR'].exportCsv({
                filename: '',
                original: false
            });
        },
        on_upload() {
            alert('upload be cliced')
        },
        on_form_query() {
            this.$refs.Dtgrd_CFR.dataChange(1);
        },
        on_form_refresh(){
            this.queryForm ={
                cashFlowTypes: '',
                dealNo: '',
                fundCodes: '',
                settleDate: ''
            }
        },
        cashModify(){
            let selects = this.$refs.Dtgrd_CFR.$refs.gridContent.getSelection();
            if(selects.length == 0){
                this.openMask();
                this.$alert("","",{
                    confirmButtonText: "",
                    type: "error",
                    callback: action => {
                        this.closeMask();
                    }
                }).then(() => {
                    this.closeMask();
                });
                return;
            }
            Object.assign(this.cashFlowModifyInfo,selects);
            this.cashFlowInfo.cash_flow_detail_clear_status = '1'
            this.showChangeCashClearStatusDialog = true;
        },
        /*clearSuccess(){
            let selects = this.$refs.Dtgrd_CFR.$refs.gridContent.getSelection();
            if(selects.length == 0){
                this.$alert("","",{
                    confirmButtonText: "",
                    type: "error"
                });
                return;
            }
            updateClearStatus({
                cashFlowModifyList: selects,
                clearStatus: "1"
            }).then(res =>{
                if(res.data && res.data.success){
                    this.$alert(res.data.msg,"",{
                        confirmButtonText: "",
                        type: "success"
                    })
                    this.showChangeCashClearStatusDialog = false;
                    this.on_form_query();
                }else{
                    this.$alert(res.data.msg, "", {
                        confirmButtonText: "",
                        type: "error"
                    })
                }
            })
        },
        clearFail(){
            let selects = this.$refs.Dtgrd_CFR.$refs.gridContent.getSelection();
            if(selects.length == 0){
                this.$alert("","",{
                    confirmButtonText: "",
                    type: "error"
                });
                return;
            }
            updateClearStatus({
                cashFlowModifyList: selects,
                clearStatus: "2"
            }).then(res =>{
                if(res.data && res.data.success){
                    this.$alert(res.data.msg,"",{
                        confirmButtonText: "",
                        type: "success"
                    })
                    this.showChangeCashClearStatusDialog = false;
                    this.on_form_query();
                }else{
                    this.$alert(res.data.msg, "", {
                        confirmButtonText: "",
                        type: "error"
                    })
                }
            })
        },
        clearBack(){

        },*/
        cancel(){
            this.showChangeCashClearStatusDialog = false;
        },
        save(){
            if(this.cashFlowInfo.cash_flow_detail_clear_status){
                updateClearStatus({
                    cashFlowModifyList: this.cashFlowModifyInfo,
                    clearStatus: this.cashFlowInfo.cash_flow_detail_clear_status
                }).then(res =>{
                    if(res.data && res.data.success){
                        this.$alert(res.data.msg,"",{
                            confirmButtonText: "",
                            type: "success"
                        })
                        this.showChangeCashClearStatusDialog = false;
                        this.on_form_query();
                    }else{
                        this.$alert(res.data.msg, "", {
                            confirmButtonText: "",
                            type: "error"
                        })
                    }
                })
            }
        },
        re_layout(){
            let wrapper0Height = document.getElementById("wrapper-0") ? document.getElementById("wrapper-0").offsetHeight : 0;
            this.autoHeight = document.getElementById("full-h-wrapper").offsetHeight - wrapper0Height - 90;
        }
    }
}
</script>

<style>

    .mr10 {
        margin-right: 10px;
    }

    .h-form .h-form-item-label {
        line-height: 32px;
    }

</style>



