<template>
  <h-row>
    <h-col span="12">
      <div>form</div>
      <h-panel>
        <h-form :model="formItem" :label-width="80">
          <h-form-item label="">
            <h-input v-model="formItem.input" placeholder="" :style="{width: inputWidth +'px', color: 'red'}"></h-input>
              {{formItem.input}}
          </h-form-item>
          <h-form-item label="">
             <h-select v-model="formItem.select" placeholder="" filterable>
              <h-option v-for="(item, index) in list" :key="index" :value="item.value"> 
                 {{ item.label }}
              </h-option> 
            </h-select> 
          </h-form-item> 
           <h-form-item label="" v-show="isShowRadio">
            <h-radio-group v-model="formItem.radio">
                <h-radio label="male"></h-radio>
                <h-radio label="female"></h-radio>
            </h-radio-group>
          </h-form-item>
          <h-form-item v-if="isShowButton">
            <h-button type="primary" @click="handleSubmit"></h-button>
            <h-button type="ghost" style="margin-left: 8px"></h-button>
          </h-form-item>  
        </h-form>
      </h-panel>
    </h-col>
  </h-row>
</template>
<script>
  import HPanel from '@/components/HPanel'
  export default {
    data () {
      return  {
      	message: '',
        formItem: {
          input: '',
          select: '',
          radio: ''
        },
        list: [
          {value:'beijing', label:''},
          {value: 'shanghai', label: ''},
          {value: 'hangzhou', label: ''}
        ],
        isShowRadio: true,
        isShowButton: true
      }
    },
    components: {
      HPanel
    },
    methods: {
      handleSubmit () {
      }
    }
  }
</script>


